.tu-progress {
  position: relative;
  line-height: 1; }
  .tu-progress__text {
    font-size: 14px;
    color: #8394be;
    display: inline-block;
    vertical-align: middle;
    margin-left: 10px;
    line-height: 1; }
    .tu-progress__text i {
      vertical-align: middle;
      display: block; }
  .tu-progress--circle, .tu-progress--dashboard {
    display: inline-block; }
    .tu-progress--circle .tu-progress__text, .tu-progress--dashboard .tu-progress__text {
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      text-align: center;
      margin: 0;
      transform: translate(0, -50%); }
      .tu-progress--circle .tu-progress__text i, .tu-progress--dashboard .tu-progress__text i {
        vertical-align: middle;
        display: inline-block; }
    .tu-progress--circle .tu-progress-circle__outer, .tu-progress--dashboard .tu-progress-circle__outer {
      position: absolute;
      top: 0;
      display: block;
      width: 100%;
      height: 100%;
      overflow: hidden; }
      .tu-progress--circle .tu-progress-circle__outer::before, .tu-progress--dashboard .tu-progress-circle__outer::before {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        width: 100%;
        height: 100%;
        box-shadow: inset 4px 4px 4px #c8d0e7, inset -2px -2px 3px #ffffff; }
      .tu-progress--circle .tu-progress-circle__outer::after, .tu-progress--dashboard .tu-progress-circle__outer::after {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        width: calc(100% - var(--stroke-width));
        height: calc(100% - var(--stroke-width));
        box-shadow: 3px 3px 4px #c8d0e7, -1px -1px 4px #ffffff; }
    .tu-progress--circle .tu-progress-circle__inner, .tu-progress--dashboard .tu-progress-circle__inner {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 2; }
  .tu-progress--without-text .tu-progress__text {
    display: none; }
  .tu-progress--without-text .tu-progress-bar {
    padding-right: 0;
    margin-right: 0;
    display: block; }
  .tu-progress--text-inside .tu-progress-bar {
    padding-right: 0;
    margin-right: 0; }
  .tu-progress.is-success .tu-progress-bar__inner {
    background: linear-gradient(-1deg, #3d8618 0%, #6bc43f 50%, #93e769 100%); }
  .tu-progress.is-success .tu-progress__text {
    color: #6bc43f; }
  .tu-progress.is-warning .tu-progress-bar__inner {
    background: linear-gradient(-1deg, #b67617 0%, #e6a23c 50%, #ffc979 100%); }
  .tu-progress.is-warning .tu-progress__text {
    color: #e6a23c; }
  .tu-progress.is-exception .tu-progress-bar__inner {
    background: linear-gradient(-1deg, #c44444 0%, #f56c6c 50%, #ff9f9f 100%); }
  .tu-progress.is-exception .tu-progress__text {
    color: #f56c6c; }

.tu-progress-bar {
  padding-right: 50px;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  margin-right: -55px;
  box-sizing: border-box; }
  .tu-progress-bar__outer {
    height: 6px;
    border-radius: 100px;
    overflow: hidden;
    position: relative;
    vertical-align: middle;
    box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff; }
  .tu-progress-bar__inner {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    text-align: right;
    border-radius: 100px;
    line-height: 1;
    white-space: nowrap;
    transition: width 0.6s ease;
    background: linear-gradient(-1deg, #510ccf 0%, #6d5dfc 50%, #8abdff 100%); }
    .tu-progress-bar__inner::after {
      display: inline-block;
      content: "";
      height: 100%;
      vertical-align: middle; }
  .tu-progress-bar__innerText {
    display: inline-block;
    vertical-align: middle;
    color: #ffffff;
    font-size: 12px;
    margin: 0 5px; }

@keyframes progress {
  0% {
    background-position: 0 0; }
  100% {
    background-position: 32px 0; } }
